.el-icon-loading {
  -webkit-animation: rotating 2s linear infinite;
  animation: rotating 2s linear infinite;
}

.el-icon--right {
  margin-left: 0.090909rem;
}

.el-icon--left {
  margin-right: 0.090909rem;
}

@-webkit-keyframes rotating {
  0% {
    -webkit-transform: rotateZ(0);
    transform: rotateZ(0);
  }

  100% {
    -webkit-transform: rotateZ(360deg);
    transform: rotateZ(360deg);
  }
}

@keyframes rotating {
  0% {
    -webkit-transform: rotateZ(0);
    transform: rotateZ(0);
  }

  100% {
    -webkit-transform: rotateZ(360deg);
    transform: rotateZ(360deg);
  }
}

.el-pagination {
  white-space: nowrap;
  padding: 0.036364rem 0.090909rem;
  color: #303133;
  font-weight: 700;
}

.el-pagination::after,
.el-pagination::before {
  display: table;
  content: "";
}

.el-pagination::after {
  clear: both;
