.el-upload {
  display: inline-block;
  text-align: center;
  cursor: pointer;
  outline: 0;
}

.el-upload__input {
  display: none;
}

.el-upload__tip {
  font-size: 0.218182rem;
  color: #606266;
  margin-top: 0.127273rem;
}

.el-upload iframe {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  opacity: 0;
  filter: alpha(opacity=0);
}

.el-upload--picture-card {
  background-color: #fbfdff;
  border: 0.018182rem dashed #c0ccda;
  border-radius: 0.109091rem;
  box-sizing: border-box;
  width: 2.690909rem;
  height: 2.690909rem;
  line-height: 2.654545rem;
  vertical-align: top;
}

.el-upload--picture-card i {
  font-size: 0.509091rem;
  color: #8c939d;
}

.el-upload--picture-card:hover,
.el-upload:focus {
  border-color: #409EFF;
  color: #409EFF;
}

.el-upload:focus .el-upload-dragger {
  border-color: #409EFF;
}

.el-upload-dragger {
  background-color: #fff;
  border: 0.018182rem dashed #d9d9d9;
  border-radius: 0.109091rem;
  box-sizing: border-box;
  width: 6.545455rem;
  height: 3.272727rem;
  text-align: center;
  position: relative;
  overflow: hidden;
}

.el-upload-dragger .el-icon-upload {
  font-size: 1.218182rem;
  color: #C0C4CC;
  margin: 0.727273rem 0 0.290909rem;
  line-height: 0.909091rem;
}

.el-upload-dragger+.el-upload__tip {
  text-align: center;
}

.el-upload-dragger~.el-upload__files {
  border-top: 0.018182rem solid #DCDFE6;
  margin-top: 0.127273rem;
  padding-top: 0.090909rem;
}

.el-upload-dragger .el-upload__text {
  color: #606266;
  font-size: 0.254545rem;
  text-align: center;
}

.el-upload-dragger .el-upload__text em {
  color: #409EFF;
  font-style: normal;
}

.el-upload-dragger:hover {
  border-color: #409EFF;
}

.el-upload-dragger.is-dragover {
  background-color: rgba(32,159,255,.06);
  border: 0.036364rem dashed #409EFF;
}

.el-upload-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.el-upload-list__item {
  -webkit-transition: all .5s cubic-bezier(.55,0,.1,1);
  transition: all .5s cubic-bezier(.55,0,.1,1);
  font-size: 0.254545rem;
  color: #606266;
  line-height: 1.8;
  margin-top: 0.090909rem;
  position: relative;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border-radius: 0.072727rem;
  width: 100%;
}

.el-upload-list__item .el-progress {
  position: absolute;
  top: 0.363636rem;
  width: 100%;
}

.el-upload-list__item .el-progress__text {
  position: absolute;
  right: 0;
  top: -0.236364rem;
}

.el-upload-list__item .el-progress-bar {
  margin-right: 0;
  padding-right: 0;
}

.el-upload-list__item:first-child {
  margin-top: 0.181818rem;
}

.el-upload-list__item .el-icon-upload-success {
  color: #67C23A;
}

.el-upload-list__item .el-icon-close {
  display: none;
  position: absolute;
  top: 0.090909rem;
  right: 0.090909rem;
  cursor: pointer;
  opacity: .75;
  color: #606266;
}

.el-upload-list__item .el-icon-close:hover {
  opacity: 1;
}

.el-upload-list__item .el-icon-close-tip {
  display: none;
  position: absolute;
  top: 0.090909rem;
  right: 0.090909rem;
  font-size: 0.218182rem;
  cursor: pointer;
  opacity: 1;
  color: #409EFF;
}

.el-upload-list__item:hover {
  background-color: #F5F7FA;
}

.el-upload-list__item:hover .el-icon-close {
  display: inline-block;
}

.el-upload-list__item:hover .el-progress__text {
  display: none;
}

.el-upload-list__item.is-success .el-upload-list__item-status-label {
  display: block;
}

.el-upload-list__item.is-success .el-upload-list__item-name:focus,
.el-upload-list__item.is-success .el-upload-list__item-name:hover {
  color: #409EFF;
  cursor: pointer;
}

.el-upload-list__item.is-success:focus:not(:hover) .el-icon-close-tip {
  display: inline-block;
}

.el-upload-list__item.is-success:active .el-icon-close-tip,
.el-upload-list__item.is-success:focus .el-upload-list__item-status-label,
.el-upload-list__item.is-success:hover .el-upload-list__item-status-label,
.el-upload-list__item.is-success:not(.focusing):focus .el-icon-close-tip {
  display: none;
}

.el-upload-list.is-disabled .el-upload-list__item:hover .el-upload-list__item-status-label {
  display: block;
}

.el-upload-list__item-name {
